import {useEffect, useState} from "react";

export const TopBar = () => {
  const [info, setInfo] = useState({
    social: []
  });

  const fetchInfo = () => {
    fetch('/information.json').then(response => response.json())
      .then(data => {
        setInfo(data);
      })
  }

  useEffect(() => {
    fetchInfo();
  }, []);

  const renderSocialIcons = Object.entries(info.social).map(([key, value]) => value ?
    <li key={key}><a href={value}><span className={`ti ti-${key}`}/></a></li> : null);

  return <div className="topbar">
    <div className="container">
      <div className="row">
        <div className="col-lg-6 col-md-6 xs-mb-10">
          <div className="topbar-call text-center text-md-start">
            <ul>
              <li><i className="fa fa-envelope-o theme-color"/>{'' || info.email}</li>
              <li><i className="fa fa-phone"/> <a href="tel:+7042791249"> <span>{'' || info.phone}</span> </a></li>
            </ul>
          </div>
        </div>
        <div className="col-lg-6 col-md-6">
          <div className="topbar-social text-center text-md-end">
            <ul>
              {renderSocialIcons}
            </ul>
          </div>
        </div>
      </div>
    </div>
  </div>
}