export const TextOverlayImagePost = props => {
  return <div className="item">
    <div className="blog-overlay">
      <div className="blog-image">
        <img className="img-fluid"
             src={props.src}
             alt=""/>
      </div>
      <div className="blog-name">
        <a className="tag" href={props.url}>{props.tag}</a>
        <h4 className="mt-15 text-white"><a href={props.url}>{props.title}</a></h4>
        <span className="text-white">{props.author} / <a href={props.url}>{props.category}</a></span>
      </div>
    </div>
  </div>
}