import {availableColor, selectColor, setColor} from "./color-changer-slice";
import {useDispatch, useSelector} from "react-redux";
import {useEffect} from "react";

export const ColorChanger = props => {
  const selectedColor = useSelector(selectColor);
  const dispatch = useDispatch();

  useEffect(() => {
    require(`../../../assets/css/skins/skin-${selectedColor}.css`);
  }, [selectedColor]);

  return <>
    <h3>Color Schemes</h3>
    <p>Which template color you want to use? Here are some predefined colors.</p>
    <ul className="styleChange clearfix">
      {availableColor.map(color => {
        return (
          <li key={color}
              className={`skin-${color} ${selectedColor === color ? 'selected' : ''}`}
              onClick={() => {
                dispatch(setColor(color))
              }}
              title={`skin-${color}`}/>
        )
      })}
    </ul>
  </>
}