import {ChildItem} from "./child-item";

export const MenuItem = ({item}) => {
  item.columns = item.columns || 1;

  const childItems = item.items.map((childItem, index) => (
    <ChildItem key={index} item={childItem}/>
  ))

  return (
    <li className="hoverTrigger ">
      <a href={item.url}>{item.title} <i className={item.class || 'fa fa-angle-down fa-indicator'}/></a>
      {
        item.items && (
          item.level ?
            <ul className="drop-down-multilevel effect-expand-top">
              {
                childItems
              }
            </ul> :
            <div className={`drop-down grid-col-${3 * item.columns} effect-expand-top`}>
              <div className="grid-row">
                <div className={`grid-col-${12 / item.columns}`}>
                  <ul>
                    {childItems}
                  </ul>
                </div>
              </div>
            </div>
        )
      }
    </li>
  )
}