import {ChildSection} from "../../ui/child-section";
import {Carousel} from "react-responsive-carousel";
import {TextOverlayImagePost} from "../../ui/text-overlay-image-post";

export const MainSection = props => {
  const carouselSetting = {
    showArrows: false,
    showStatus: false,
    infiniteLoop: true,
    emulateTouch: true,
    autoPlay: true,
    showThumbs: false
  }

  const items = [{
    src: '/images/01.jpg',
    tag: 'Fashion',
    title: 'Does your life lack meaning',
    category: 'Business',
    author: 'By adimn',
    url: '#'
  }, {
    src: '/images/02.jpg',
    tag: 'Style',
    title: 'Supercharge your motivation',
    category: 'Business',
    author: 'By adimn',
    url: '#'
  }];
  const allItems = items.map((item, index) => <TextOverlayImagePost key={index} {...item}/>);

  return <section className="page-section-ptb white-bg o-hidden">
    <div className="container">
      <div className="row">
        <div className="col-lg-9">
          <div className="row">
            <div className="col-lg-7">
              <ChildSection title="Today's highlights">
                <Carousel {...carouselSetting}>
                  {allItems}
                </Carousel>
              </ChildSection>
            </div>
            <div className="col-lg-5 col-mt-30">
              <ChildSection title="Latest Posts">
                <div className="sidebar-widget">
                  <div className="clearfix medium recent-post">
                    <div className="recent-post-image">
                      <img className="img-fluid"
                           src="https://themes.potenzaglobalsolutions.com/html/webster-responsive-multi-purpose-html5-template/demo-categories/magazine/images/blog/01.jpg"
                           alt=""/>
                    </div>
                    <div className="recent-post-info">
                      <a href="#">Nibh vel velit auctor aliquet Aenean</a>
                      <span><i className="fa fa-calendar-o"/> September 30, 2021</span>
                    </div>
                  </div>

                  <div className="recent-post medium clearfix">
                    <div className="recent-post-image">
                      <img className="img-fluid"
                           src="https://themes.potenzaglobalsolutions.com/html/webster-responsive-multi-purpose-html5-template/demo-categories/magazine/images/blog/03.jpg"
                           alt=""/>
                    </div>
                    <div className="recent-post-info">
                      <a href="#">Aenean auctor alique Nibh vel velit </a>
                      <span><i className="fa fa-calendar-o"/> September 30, 2021</span>
                    </div>
                  </div>
                  <div className="recent-post medium clearfix">
                    <div className="recent-post-image">
                      <img className="img-fluid"
                           src="https://themes.potenzaglobalsolutions.com/html/webster-responsive-multi-purpose-html5-template/demo-categories/magazine/images/blog/04.jpg"
                           alt=""/>
                    </div>
                    <div className="recent-post-info">
                      <a href="#"> Velit auctor aliquet Aenean nibh vel</a>
                      <span><i className="fa fa-calendar-o"/> September 30, 2021</span>
                    </div>
                  </div>
                </div>
              </ChildSection>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
}