import {Carousel} from "react-responsive-carousel";
import {TextOverlayImagePost} from "../../ui/text-overlay-image-post";

export const Banner = () => {
  const carouselSetting = {
    showArrows: false,
    showStatus: false,
    infiniteLoop: true,
    emulateTouch: true,
    autoPlay: true,
    showThumbs: false
  }
  const items = [{
    src: '/images/01.jpg',
    tag: 'Fashion',
    title: 'Does your life lack meaning',
    category: 'Business',
    author: 'By adimn',
    url: '#'
  },{
    src: '/images/02.jpg',
    tag: 'Style',
    title: 'Supercharge your motivation',
    category: 'Business',
    author: 'By adimn',
    url: '#'
  },{
    src: '/images/03.jpg',
    tag: 'Style',
    title: 'Supercharge your motivation',
    category: 'Business',
    author: 'By adimn',
    url: '#'
  },{
    src: '/images/04.jpg',
    tag: 'Style',
    title: 'Supercharge your motivation',
    category: 'Business',
    author: 'By adimn',
    url: '#'
  },{
    src: '/images/05.jpg',
    tag: 'Style',
    title: 'Supercharge your motivation',
    category: 'Business',
    author: 'By adimn',
    url: '#'
  },];

  const allItems = items.map((item, index) => <TextOverlayImagePost key={index} {...item}/>);
  const carouselItems = allItems.slice(0, allItems.length - 3);
  const restItems = allItems.slice(allItems.length - 3, allItems.length)

  return (
    <section className="o-hidden position-relative white-bg">
      <div className="container">
        <div className="row g-0">
          <div className="col-lg-7">
            <Carousel {...carouselSetting}>
              {carouselItems}
            </Carousel>
          </div>
          <div className="col-lg-5">
            {restItems}
          </div>
        </div>
      </div>
    </section>
  )
}