import {configureStore} from '@reduxjs/toolkit';
import layoutChangerReducer from '../features/theme-changer/layout/layout-changer-slice';
import colorChangerReducer from '../features/theme-changer/color/color-changer-slice';
import backgroundChangerReducer from "../features/theme-changer/background-pattern/backgroundChangerSlice";

export const store = configureStore({
  reducer: {
    layoutChanger: layoutChangerReducer,
    colorChanger: colorChangerReducer,
    backgroundChanger: backgroundChangerReducer
  },
});
