import {useState} from "react";
import {LayoutChanger} from "./layout/layout-changer";
import {ColorChanger} from "./color/color-changer";
import {BackgroundChanger} from "./background-pattern/background-changer";

export const ThemeChanger = () => {
  const [isShow, setIsShow] = useState(false);

  return <div className={`style-customizer ${isShow ? 'opened' : 'closed'}`}
              style={{right: isShow ? '0px' : '-300px', transition: 'right 0.5s'}}>
    <div className="buy-button"><a className="opener" onClick={() => {
      setIsShow(!isShow)
    }}><i className="fa fa-cog fa-spin"/></a></div>
    <div className="clearfix content-chooser scrollbar" style={{paddingTop: "29px"}}>
      <LayoutChanger/>
      <ColorChanger/>
      <BackgroundChanger/>
    </div>
  </div>
}