export const ChildItem = ({item}) => {
  return (
    <li className="hoverTrigger">
      <a href={item.url}>
        {item.title} {
          item.badge && <span className={item.badge.class || 'badge bg-primary'}>{item.badge.title}</span>
        }
        {
          item.items && <i className="ti-plus fa-indicator"/>
        }
      </a>
      {
        item.items && <ul className="drop-down-multilevel effect-expand-top">
          {
            item.items.map((childItem, index) => <ChildItem key={index} item={childItem}/>)
          }
        </ul>
      }
    </li>
  )
}