import logos from '../images/logo';
import bannerImage from '../images/banner-02.jpg';
import {MenuItems} from "./menu-items";
import {useEffect, useState} from "react";
import {useWindowSize} from "../../../hooks/use-window-size";
import {SearchBox} from "../search-box";
import {useScroll} from "../../../hooks/use-scroll";
import {useSelector} from "react-redux";
import {selectColor} from "../../../features/theme-changer/color/color-changer-slice";

export const MegaMenu = () => {
  const [isMobileMenuClosed, setIsMobileMenuClosed] = useState(true);
  const [isMobileUI, setIsMobileUI] = useState(false);
  const [isScrolledDown, setIsScrolledDown] = useState(false);

  const selectedColor = useSelector(selectColor);

  const windowSize = useWindowSize();
  const scrollSize = useScroll();

  useEffect(() => {
    setIsScrolledDown(scrollSize.top > 150);
  }, [scrollSize.top]);

  useEffect(() => {
    setIsMobileUI(windowSize.width <= 991)
  }, [windowSize.width]);

  const toggleMobileMenu = () => {
    setIsMobileMenuClosed(!isMobileMenuClosed);
  }

  return <div className="menu">
    <nav id="menu" className={`mega-menu ${isScrolledDown ? 'mobileTopFixed desktopTopFixed' : ''}`}>
      <section className="menu-list-items">
        <div className="container">
          <div className="row">
            <div className="col-lg-12 col-md-12 position-relative">
              <div className="mt-0">
                <ul className="menu-logo">
                  <li>
                    <a href="/"><img id="logo_img" src={logos[selectedColor]} alt="logo"/></a>
                    <div className={`menu-mobile-collapse-trigger ${isMobileMenuClosed ? '' : 'active'}`}
                         onClick={() => {
                           toggleMobileMenu()
                         }}><span/></div>
                  </li>
                </ul>
                <div className="add-banner">
                  <a href="#">
                    <img className="img-fluid" src={bannerImage} alt=""/>
                  </a>
                </div>
              </div>
              <div className="menu-bar">
                <ul className="menu-links" style={{
                  transition: "opacity 0.3s ease-in-out",
                  opacity: (isMobileUI && isMobileMenuClosed) ? 0 : 1,
                  maxHeight: "400px",
                  overflow: "auto"
                }}>
                  <MenuItems/>
                </ul>
                <SearchBox/>
              </div>
            </div>
          </div>
        </div>
      </section>
    </nav>
  </div>
}