import {createSlice} from "@reduxjs/toolkit";

export const availableColor = ['default',
  'blue',
  'brown',
  'cyan',
  'dark-pink',
  'gold',
  'gray',
  'green-dark',
  'lime',
  'olive',
  'orange',
  'pink',
  'purple',
  'red',
  'green',
  'salmon',
  'steelblue',
  'water-blue',
  'yellow',
  'persian-green',
  'perfume',
  'chestnut-rose',
  'blue-gem',
  'malachite'
]

const initialState = {
  value: 'default'
}

const savedState = {
  value: localStorage.getItem('color')
}

export const colorChangerSlice = createSlice({
  name: 'colorChanger',
  initialState: savedState.value ? savedState : initialState,
  reducers: {
    setColor: (state, action) => {
      const newColor = action.payload;
      if (availableColor.includes(newColor)) {
        state.value = newColor;
      } else {
        state.value = availableColor[0]
      }

      localStorage.setItem('color', state.value);
    }
  }
});

export const {setColor} = colorChangerSlice.actions;

export const selectColor = (state) => state.colorChanger.value;

export default colorChangerSlice.reducer;