import {useState} from "react";

export const SearchBox = props => {
  const [isOpen, setIsOpen] = useState(false);

  const toggleSearchBox = () => {
    setIsOpen(!isOpen);
  }

  const onSearch = (e) => {
    e.preventDefault();
  }

  return (
    <div className="search-cart">
      <div className={`search ${isOpen ? 'search-open' : ''}`}>
        <div className="search-btn not_click" onClick={() => toggleSearchBox()}/>
        <div className="search-box not-click">
          <form onSubmit={onSearch}>
            <input type="text" className="not-click form-control" name="search" placeholder="Search.." value=""/>
              <button className="search-button" type="submit"><i className="fa fa-search not-click"/></button>
          </form>
        </div>
      </div>
    </div>
  )
}