import {availableImages, availablePatterns, selectBackground, setBackground} from "./backgroundChangerSlice";
import {useDispatch, useSelector} from "react-redux";
import {useEffect} from "react";
import backgrounds from "./background";

export const BackgroundChanger = () => {
  const selectedBackground = useSelector(selectBackground);
  const dispatch = useDispatch();

  useEffect(() => {
    availablePatterns.concat(availableImages).forEach(background => document.body.classList.remove(background));
    document.body.classList.add(selectedBackground);
  }, [selectedBackground]);

  return <>
    <h3>Background Patterns</h3>
    <p>Which background pattern you want to use?</p>
    <ul className="patternChange clearfix">
      {
        availablePatterns.map(background => {
          return (
            <li key={background} className={background} title={background}
                onClick={() => dispatch(setBackground(background))}/>
          )
        })
      }
    </ul>

    <h3>Background Images</h3>
    <p>Which background image you want to use?</p>
    <ul className="patternChange main-bg-change clearfix">
      {
        availableImages.map(image => {
          return (
            <li key={image} className={image} title={image} onClick={() => dispatch(setBackground(image))}>
              <img
                src={backgrounds[`${image}-thumb`]}
                alt=""/>
            </li>
          )
        })
      }
    </ul>
    <ul className="resetAll">
      <li><a className="button button-border button-reset" href="#">Reset All</a></li>
    </ul>
  </>
}