import {createSlice} from "@reduxjs/toolkit";

export const availablePatterns = [
  'pattern-0',
  'pattern-1',
  'pattern-2',
  'pattern-3',
  'pattern-4',
  'pattern-5',
  'pattern-6',
  'pattern-7',
  'pattern-8',
  'pattern-9',
  'pattern-10',
  'pattern-11',
  'pattern-12',
  'pattern-13',
  'pattern-14',
  'pattern-15'
];

export const availableImages = [
  'main-bg-1',
  'main-bg-2',
  'main-bg-3',
  'main-bg-4',
  'main-bg-5',
  'main-bg-6',
  'main-bg-7',
  'main-bg-8',
  'main-bg-9',
  'main-bg-10'
]

const initialState = {
  value: 'pattern-0'
}

const savedState = {
  value: localStorage.getItem('background')
}

export const backgroundChangerSlice = createSlice({
  name: 'backgroundChanger',
  initialState: savedState.value ? savedState : initialState,
  reducers: {
    setBackground: (state, action) => {
      const newBackground = action.payload;
      if (availablePatterns.includes(newBackground) || availableImages.includes(newBackground)) {
        state.value = newBackground;
      } else {
        state.value = availablePatterns[0];
      }

      localStorage.setItem('background', state.value);
    }
  }
});

export const {setBackground} = backgroundChangerSlice.actions;

export const selectBackground = state => state.backgroundChanger.value;

export default backgroundChangerSlice.reducer;