import {useDispatch, useSelector} from "react-redux";
import {availableLayouts, selectLayout, setLayout} from "./layout-changer-slice";
import {useEffect} from "react";

const capitalize = (s) => {
  return s.charAt(0).toUpperCase() + s.slice(1);
}

export const LayoutChanger = () => {
  const selectedLayout = useSelector(selectLayout);
  const dispatch = useDispatch();

  useEffect(() => {
    availableLayouts.map(layout => layout + '-layout').forEach(al => {
      document.body.classList.remove(al);
    });
    document.body.classList.add(selectedLayout + '-layout');
    localStorage.setItem('layout', selectedLayout + '');
  }, [selectedLayout]);

  return <>
    <h3>Layout Options</h3>
    <p>Which layout option you want to use?</p>
    <ul className="layoutstyle clearfix">
      {
        availableLayouts.map(al => {
          return (
            <li className={`${al}-layout ${selectedLayout === al ? 'selected' : ''}`}
                title={al} key={al}
                onClick={() => {
                  dispatch(setLayout(al))
                }}>{capitalize(al)}</li>
          )
        })
      }
    </ul>
  </>
}