import {createSlice} from "@reduxjs/toolkit";

export const availableLayouts = ['wide', 'boxed', 'frame'];

const initialState = {
  value: 'wide'
}

const savedState = {
  value: localStorage.getItem('layout')
}

export const layoutChangerSlice = createSlice({
  name: 'layoutChanger',
  initialState: savedState.value ? savedState : initialState,
  reducers: {
    setLayout: (state, action) => {
      const newLayout = action.payload;
      if (availableLayouts.includes(newLayout)) {
        state.value = newLayout;
      } else {
        state.value = availableLayouts[0]
      }
    }
  }
});

export const {setLayout} = layoutChangerSlice.actions;

export const selectLayout = (state) => state.layoutChanger.value;

export default layoutChangerSlice.reducer;